/* 正菜 */


/* checkbox 1 */

.checkbox {
    margin-right: 10px;
}

.checkbox span {
    display: inline-block;
    position: relative;
    height: 20px;
    padding-left: 28px;
}

.checkbox span:before {
    content: ' ';
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 20px;
    height: 20px;
    border-radius: 3px;
    background: #7B7EA2;
    -webkit-box-shadow: 0 0 2px 2px rgba(112, 115, 148, .4);
    -moz-box-shadow: 0 0 2px 2px rgba(112, 115, 148, .4);
    box-shadow: 0 0 2px 2px rgba(112, 115, 148, .4);
    -webkit-transition: background ease-in-out 0.4s;
    -moz-transition: background ease-in-out 0.4s;
    transition: background ease-in-out 0.4s;
}

.checkbox span:after {
    content: ' ';
    position: absolute;
    left: 3px;
    top: 3px;
    z-index: 2;
    width: 14px;
    height: 14px;
    border-radius: 2px;
    background: #E6E6EB;
    -webkit-box-shadow: 0 0 2px 2px rgba(112, 115, 148, .4);
    -moz-box-shadow: 0 0 2px 2px rgba(112, 115, 148, .4);
    box-shadow: 0 0 2px 2px rgba(112, 115, 148, .4);
    -webkit-transition: background ease-in-out 0.4s;
    -moz-transition: background ease-in-out 0.4s;
    transition: background ease-in-out 0.4s;
}

.checkbox input:checked~span:after {
    /*加勾*/
    content: '\2714';
    color: #fff;
    line-height: 6px;
    font-size: 22px;
    background: #7B7EA2;
    -webkit-box-shadow: 0 0 2px 2px rgba(112, 115, 148, .0);
    -moz-box-shadow: 0 0 2px 2px rgba(112, 115, 148, .0);
    box-shadow: 0 0 2px 2px rgba(112, 115, 148, .0);
}

.checkbox input:checked~span:before {
    background: #E6E6EB;
}


/* checkbox 2 */

.checkbox-2 {
    margin-right: 10px;
}

.checkbox-2 span {
    display: inline-block;
    position: relative;
    height: 20px;
    padding-left: 40px;
}

.checkbox-2 span:before {
    content: ' ';
