    left: 4px;
    top: 3px;
    z-index: 2;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #E6E6EB;
    -webkit-box-shadow: 0 0 2px 2px rgba(112, 115, 148, .4);
    -moz-box-shadow: 0 0 2px 2px rgba(112, 115, 148, .4);
    box-shadow: 0 0 2px 2px rgba(112, 115, 148, .4);
    -webkit-transition: left ease-in-out 0.3s;
    -moz-transition: left ease-in-out 0.3s;
    transition: left ease-in-out 0.3s;
}

.checkbox-2 input:checked~span:after {
    left: 17px;
}


